  padding: 0px 0px 0px 0px;
  font-size: 16px;
  line-height: 24px;
  font-family: Arial, Helvetica, sans-serif;
  color: #323336;
}

a:hover,
input:hover,
button:hover {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  color: #fd823f;
}

.cta-button {
  margin: 0px 0px 0px 0px;
  padding: 0px 0px 0px 0px;
  border: none;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 32px;
  padding-right: 32px;
