  color: white;
  font-size: 16px;
  font-family: Arial, Helvetica, sans-serif;
  border-radius: 4px;
}

.icon-sprite {
  fill: #a8a8a8;
}

.back-forward-btn {
  border: 0px;
  height: 100%;
  background: none;
  width: 64px;
}

.back-forward-btn:after {
  content: "";
  background-color: rgba(50, 51, 54, 0.16);
  background-image: url("../img/global/sliderArrow/arrowRight--white.svg");
  background-repeat: no-repeat;
  background-position: center;
  width: 40px;
  height: 40px;
  border-radius: 100px;
  position: absolute;
  left: 50%;
  margin-left: -20px;
  top: 50%;
  margin-top: -20px;
}

.back-forward-btn:hover {
  background-color: rgba(50, 51, 54, 0.16);
  border-bottom-right-radius: 8px;
  border-top-right-radius: 8px;
}

.back-forward-btn:hover::after {
  background-color: rgba(50, 51, 54, 0.4);
}

.back-forward-btn--slider-goods:after {
  content: "";
  background-color: #f1f1f1;
  background-image: url("../img/global/sliderArrow/arrowRight--dark.svg");
}

.back-forward-btn--slider-goods:hover {
  border-radius: 8px;
}

.back-forward-btn--slider-goods:hover::after {
  background-color: rgba(168, 168, 168, 0.4);
}

.tab {
  list-style: none;
  display: flex;
  margin: 0px 0px 0px 0px;
  padding: 0px 0px 0px 0px;
  padding-bottom: 12px;
  align-items: baseline;
  justify-content: space-between;
  border-bottom: 1px solid #cdcdcd;
}

.tab__item {
  padding-left: 8px;
  padding-right: 16px;
}

.tab__item:nth-child(1) {
  padding-left: 0px;
}

.tab__item:last-child {
  padding-right: 0px;
  text-align: right;
}

.tab__item--link {
  margin-left: auto;
}

.tab__link {
  color: #323336;
  text-decoration: none;
  display: block;
  line-height: 32px;
  position: relative;
}

.tab__link--active {
  font-weight: bold;
}

.tab__link--active::after {
  content: "";
  position: absolute;
  display: block;
  left: 0px;
  bottom: -13px;
  width: 100%;
  height: 4px;
  background-color: #fd823f;
}

.tab__title {
  margin: 0px 0px 0px 0px;
  padding: 0px 0px 0px 0px;
  margin-bottom: 0px;
  width: 320px;
}

.card {
  display: flex;
  list-style: none;
  margin: 0px 0px 0px 0px;
  padding: 0px 0px 0px 0px;
  margin-top: 32px;
  justify-content: space-between;
}

.card__item {
  width: 270px;
  border: 1px solid #cdcdcd;
  border-radius: 8px;
  position: relative;
}

.card__item--sale::after {
  content: "Акция";
  transform: rotate(-90deg);
  position: absolute;
  font-size: 12px;
  font-weight: bold;
  color: white;
  text-transform: uppercase;
  top: 23px;
  left: -25px;
  letter-spacing: 2px;
  display: block;
  width: auto;
  padding-top: 2px;
  padding-bottom: 2px;
  padding-left: 12px;
  padding-right: 12px;
  height: 24px;
  background: linear-gradient(91.1deg, #9710e9 1.17%, #fd8d43 99.31%);
  border-top-right-radius: 8px;
  border-bottom-left-radius: 8px;
}

.card__img-container {
  display: flex;
  width: 270px;
  justify-content: center;
  align-items: flex-end;
  height: 240px;
}

.card__img {
  display: block;
  margin: 0px 0px 0px 0px;
  padding: 0px 0px 0px 0px;
}

.card__title-container {
  text-align: center;
  padding-left: 24px;
  padding-right: 24px;
  padding-bottom: 8px;
}

.card__link {
  color: #323336;
  text-decoration: none;
}

.card__title {
  font-size: 16px;
  line-height: 20px;
  font-weight: normal;
}

.card__price {
  font-size: 24px;
  line-height: 24px;
  font-weight: bold;
}

.card__price--out {
  color: #a8a8a8;
}

.card__btn-container {
  margin: 0px 0px 0px 0px;
  padding: 0px 0px 0px 0px;
  display: flex;
  width: 100%;
  justify-content: flex-end;
}

.card__buy {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 40px;
  background: #fd823f;
  border: 1px solid #fd823f;
  border-bottom-right-radius: 8px;
  border-top-left-radius: 8px;
  color: #323336;
  text-decoration: none;
}

.card__notify-me {
  display: flex;
  width: 100%;
  justify-content: center;
}

.card__favourite {
  position: absolute;
  top: 8px;
  right: 8px;
  margin: 0px 0px 0px 0px;
  padding: 0px 0px 0px 0px;
  display: flex;
  width: 40px;
  height: 40px;
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

