.promo-banner {
  margin: 0px 0px 0px 0px;
  padding: 0px 0px 0px 0px;
  flex-shrink: 0;
  display: flex;
  width: 1170px;
  margin: auto;
  margin-bottom: 80px;
}

.banner {
  margin: 0px 0px 0px 0px;
  padding: 0px 0px 0px 0px;
}

.banner__img {
  margin: 0px 0px 0px 0px;
  padding: 0px 0px 0px 0px;
  border-radius: 8px;
}

.complementary-container {
  margin-top: 80px;
  margin-bottom: 80px;
}

.complementary-goods-container {
  flex-shrink: 0;
  display: flex;
  width: 1170px;
  margin: auto;
  justify-content: space-between;
  position: relative;
}

.complementary-goods {
  margin: 0px 0px 0px 0px;
  padding: 0px 0px 0px 0px;
  width: 100%;
}

.complementary-goods--title-without-link-right {
  width: 500px;
}

.card-container {
  position: relative;
}

.slider-popular__button {
  display: block;
  position: absolute;
  top: 0px;
  right: 0px;
}

.slider-popular__control-back {
  display: block;
  position: absolute;
  top: 0px;
  left: -72px;
  transform: rotate(-180deg);
}

.slider-popular__control-forward {
  top: 0px;
  right: -72px;
}

.footer-site {
  background-color: #f1f1f1;
}

.footer-container {
  flex-shrink: 0;
  display: flex;
  width: 1170px;
  margin: auto;
  flex-wrap: wrap;
  padding-top: 42px;
  padding-bottom: 24px;
}

.email {
  margin: 0px 0px 0px 0px;
  padding: 0px 0px 0px 0px;
  width: 320px;
  padding-right: 160px;
}

.email__title {
  margin: 0px 0px 0px 0px;
  padding: 0px 0px 0px 0px;
  display: block;
  font-size: 14px;
  line-height: 20px;
  margin-bottom: 16px;
  padding-right: 88px;
}

.email__form {
  margin: 0px 0px 0px 0px;
  padding: 0px 0px 0px 0px;
  position: relative;
}

.email__input {
  width: 100%;
  border: none;
  background-color: white;
  border-radius: 4px;
  padding-left: 8px;
  height: 40px;
  font-size: 14px;
  line-height: 32px;
  border: 1px solid #cdcdcd;
  font-family: Arial, Helvetica, sans-serif;
  box-sizing: border-box;
}

.email__button {
  position: absolute;
  top: 6px;
  right: 6px;
  padding-top: 6px;
  padding-bottom: 6px;
  font-size: 14px;
  padding-left: 12px;
  padding-right: 12px;
}

.email__copy-text {
  margin: 0px 0px 0px 0px;
  padding: 0px 0px 0px 0px;
  margin-top: 32px;
  font-size: 14px;
  color: #656464;
}

.footer-menu-container {
  flex-grow: 1;
  display: flex;
}

.footer-menu__title {
  margin: 0px 0px 0px 0px;
  padding: 0px 0px 0px 0px;
  font-size: 14px;
  display: block;
  margin-bottom: 16px;
}

.footer-menu__list {
  font-size: 14px;
  list-style-type: none;
  margin: 0px 0px 0px 0px;
  padding: 0px 0px 0px 0px;
  margin-right: 104px;
}

.footer-menu__link {
  color: #323336;
  text-decoration: none;
}

.two-columns {
  margin: 0px 0px 0px 0px;
  padding: 0px 0px 0px 0px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.two-columns-space {
  margin-right: 32px;
}

.two-columns-container {
  display: flex;
}